import './GameCard.css';

const GameCard = ({isClosed, index, clickHandler}) => {
  console.log(isClosed)
  return <div className="picture" onClick={() => clickHandler(index)}>
    <img 
      alt={index}
      className='cardImage'
      src="https://fullbellylaughs.com/uploads/landingpageimages/Bernard.png"
    />
    {isClosed ? <div className="closeImage">X</div> : null}
    {index%2 === 0 ? <><br /><br /></> : null}
  </div>
}

export default GameCard;