import './GameArea.css';
import GameCard from '@components/GameCard';

import { useState } from 'react';

const pictures = new Array(24).fill().map(x => ({
  name: 'Name',
  isClosed: false,
}));

const GameArea = () => {
  const [gameCards, setGameCards] = useState(pictures);
  const clickPicture = (index) => {
    const newCards = [...gameCards];
    newCards[index].isClosed = !newCards[index].isClosed;
    setGameCards(newCards);
  };

  const board = gameCards.map((picture, i) => {
    console.log(picture);
    return <GameCard 
      key={i} 
      index={i} 
      isClosed={picture.isClosed} 
      clickHandler={clickPicture}
      />
      
  })

  return <div className="wrapper">
    {board}
  </div>
}

export default GameArea;